/* src/styles.css */
body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.container {
  background: #ffffff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  width: 90%;
  max-width: 450px; /* Increase max width */
  min-width: 350px; /* Ensures it doesn’t get too small */
  text-align: center;
}

.step h2 {
  color: #333;
  margin-bottom: 15px;
}

input {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 15px;
  margin: 10px;
  cursor: pointer;
  border-radius: 5px;
  font-size: 16px;
}

button:hover {
  background-color: #0056b3;
}

/* Responsive Stepper */
.stepper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.stepper span {
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border-radius: 50%;
  background-color: #ccc;
  color: white;
  font-weight: bold;
  font-size: 14px;
}

.stepper .active {
  background-color: #007bff;
}

/* Equipment List */
.equipment-header {
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  background: #007bff;
  color: white;
  padding: 5px;
  border-radius: 5px;
}

.equipment-item {
  display: flex;
  justify-content: space-between;
  gap: 5px;
  margin: 10px 0;
  flex-wrap: wrap;
}

.equipment-item input {
  width: 22%;
  padding: 5px;
  text-align: center;
  font-size: 14px;
}

/* Make adjustments for smaller screens */
@media (max-width: 600px) {
  .container {
    width: 90%;
    padding: 15px;
  }

  button {
    width: 100%;
    margin: 5px 0;
  }

  .equipment-header {
    font-size: 14px;
    padding: 3px;
  }

  .equipment-item {
    flex-direction: column;
    gap: 10px;
  }

  .equipment-item input {
    width: 100%;
  }

  .stepper span {
    width: 25px;
    height: 25px;
    line-height: 25px;
    font-size: 12px;
  }
}
.result-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 15px;
}

.result-table td {
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.result-table tr:last-child td {
  border-bottom: none;
}

.result-table td:first-child {
  font-weight: bold;
  text-align: left;
}

.result-table td:last-child {
  text-align: right;
}

/* Installation Section*/
.installation-contact {
  margin-top: 20px;
  text-align: center;
}

.whatsapp-button {
  display: inline-flex;
  align-items: center;
  background-color: #25d366;
  color: white;
  padding: 10px 15px;
  border-radius: 5px;
  text-decoration: none;
  font-size: 16px;
  font-weight: bold;
}

.whatsapp-button i {
  margin-right: 8px;
  font-size: 20px;
}

.whatsapp-button:hover {
  background-color: #1ebea5;
}

.input-group {
  display: flex;
  align-items: center;
  gap: 10px; /* Space between label and dropdown */
  margin-bottom: 15px;
}

.input-group label {
  font-weight: bold;
  min-width: 120px; /* Adjust label width */
}

/* Toggle Switch */
.toggle-switch {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.toggle-switch input {
    display: none;
}

.toggle-switch .slider {
    width: 40px;
    height: 20px;
    background-color: #ccc;
    border-radius: 50px;
    position: relative;
    transition: 0.4s;
}

.toggle-switch input:checked + .slider {
    background-color: green;
}

.toggle-switch .slider::before {
    content: "";
    position: absolute;
    width: 16px;
    height: 16px;
    background: white;
    border-radius: 50%;
    top: 2px;
    left: 2px;
    transition: 0.4s;
}

.toggle-switch input:checked + .slider::before {
    transform: translateX(20px);
}

/* Pricing Switch */
.pricing-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 15px;
}

.pricing-table td {
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.pricing-table tr:last-child td {
  border-bottom: none;
}

.pricing-table td:first-child {
  font-weight: bold;
  text-align: left;
}

.pricing-table td:last-child {
  text-align: right;
}

button {
  width: 100%;
  max-width: 300px; /* Prevent buttons from being too large */
  padding: 12px;
  font-size: 16px;
  border-radius: 5px;
}

@media (min-width: 768px) {
  button {
    width: auto; /* Normal width for desktop */
  }
}

.stepper span {
  width: 28px;
  height: 28px;
  line-height: 28px;
  font-size: 14px;
}

@media (max-width: 600px) {
  .stepper {
    flex-wrap: wrap; /* Allow wrapping on smaller screens */
  }

  .stepper span {
    width: 22px;
    height: 22px;
    line-height: 22px;
    font-size: 12px;
  }
}

@media (max-width: 600px) {
  .equipment-item {
    flex-direction: column; /* Stack elements vertically */
    gap: 8px;
  }

  .equipment-item input {
    width: 100%; /* Full width for better readability */
  }
}
